*,
*::before,
*::after {
  box-sizing: border-box;
}

.container {
  margin-top: 100px;
  text-align: center;
  font-size: 16px;
}

body {
  background: linear-gradient(45deg, #eecfba, #c5dde8);
}

.inputText {
  height: 30px;
  width: 300px;
  outline: none;
  border-radius: 10%;
}

.list {
  list-style: none;
}

.countries-container {
  font-size: 20px;
  display: flex;
  justify-content: center;
  text-align: left;
}
